body {
  padding: 0;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}
.main {
  display: flex;
  flex-wrap: wrap;
  //flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 0 120px;
  min-height: 100vh;
  background-image: url('./background.jpg');
  background-repeat: repeat;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: flex-start;
  width: 100%;
}

.header h1,
.header p {
  display: block;
  text-align: center;
  font-weight: 700;
}

.header h1 {
  display: block;
  margin-bottom: 20px;
  text-transform: uppercase;
}
