.cat-quote__wrapper {
  margin: 20px;
  padding: 2em;
  max-width: 1200px;
  background-color: white;
  border-radius: 8px;

  &--loading {
    width: 400px;
    height: 400px;
    background-color: #E7DFDD;
    border-radius: 50%;
  }
}

.cat-quote__img {
  max-width: 100%;
}

.cat-quote__text {
  font-size: 1.2em;
  text-align: center;
}

.cat-quote__text-author {
  font-style: italic;
  text-align: right;
}
